@import "mixins";

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    background-color: $color-midnight;

    // .header__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: $offset-m 15px;
        border-bottom: 1px solid $color-border;
    }

    // .header__right

    &__right {
        display: flex;
        align-items: center;

        @include tablet {
            flex-direction: row-reverse;
        }
    }

    // .header__menu

    &__menu {
        @include tablet {
            position: fixed;
            top: 0;
            left: -100%;

            width: 100%;
            height: 100%;
            z-index: 99;

            background-color: $color-amber;
            opacity: 0;
            transition: 0.3s all ease-in;
        }

        &--active {
            left: 0;

            display: block;

            opacity: 1;
        }
    }

    //.menu__list

    .menu__list {
        @include tablet {
            width: 100%;
            height: 100%;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
        }
    }

    //.menu__item

    .menu__item {
        @include tablet {
            display: block;

            &:not(:last-child) {
                margin-right: 0;
            }
        }
    }

    //.menu__link

    .menu__link {
        @include tablet {
            font: $font-l;
            color: $color-black;
            font-weight: 700;
        }
    }
}

.menu {
    // .menu__list

    &__list {
        display: flex;
    }

    // .menu__item

    &__item {
        &:not(:last-child) {
            margin-right: $offset-l;
        }
    }

    // .menu__link

    &__link {
        position: relative;
        outline: none;
        font: $font-xs;
        color: $color-white;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: -5px;
            width: 100%;
            height: 1px;
            background-color: currentColor;
            transform: scaleX(0);
            transform-origin: left;
            transition: transform 0.3s ease-in-out;
        }

        @include desktop {
            &:hover {
                &::before {
                    transform: scaleX(1);
                }
            }
        }

        &:active {
            opacity: 0.8;
        }
    }
}

.cart {
    position: relative;
    margin-left: $offset-l;
    // .cart__btn

    &__btn {
        position: relative;
        width: 20px;
        height: 20px;

        svg {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            fill: $color-white;
            pointer-events: none;
            transition: fill 0.3s ease-in-out;
        }

        @include desktop {
            &:hover {
                svg {
                    fill: $color-amber;
                }
            }
        }

        &:focus {
            svg {
                fill: $color-amber-active;
            }
        }

        &--inactive {
            pointer-events: none;
        }
    }

    // .cart__count

    &__count {
        position: absolute;
        right: -6px;
        bottom: -6px;

        display: inline-flex;
        align-items: center;
        justify-content: center;

        border-radius: 50%;
        height: 16px;
        min-width: 16px;

        font: $font-xs;
        color: $color-white;
        background-color: $color-amber;
        pointer-events: none;

        opacity: 0;
        visibility: hidden;

        &--visible {
            opacity: 1;
            visibility: visible;
        }
    }

    @include tablet {
        margin-right: $offset-m;
        margin-left: 0;
    }
}

.mini-cart {
    position: absolute;
    right: 0%;
    top: calc(100% + 24px);
    width: 480px;
    background-color: $color-amber-hover;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    &--visible {
        opacity: 1;
        visibility: visible;
    }
    // .mini-cart__top

    &__top {
        max-height: 323px;

        .simplebar-track.simplebar-vertical {
            right: 5px;
            top: 20px;

            bottom: 20px;
            border-radius: 5px;
            width: 4px;
            background-color: rgba(77, 77, 77, 0.1);
        }

        .simplebar-scrollbar::before {
            left: 0;
            right: auto;
            border-radius: 5px;
            width: 4px;
            height: 200px;
            opacity: 1;
            background-color: rgba(77, 77, 77, 0.2);
        }
    }

    // .mini-cart__item

    &__item {
        &:not(:last-child) {
            margin-bottom: $offset-xs;
        }
    }

    // .mini-cart__bottom

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: $offset-m;
        box-shadow: 0px -4px 10px rgba(0, 13, 84, 0.1);
        background-color: $color-white;

        @include mobile {
            padding: $offset-xs;
        }
    }

    // .mini-cart__text

    &__text {
        display: block;
        margin-bottom: $offset-xs;
        font: $font-xs;
        color: $color-black;
    }

    // .mini-cart__sum

    &__sum {
        display: block;
        font: $font-m;
        color: $color-black;

        @include mobile {
            margin-bottom: $offset-xs;
        }
    }

    // .mini-cart__order

    &__order {
        display: block;
        padding: 17px 47px;

        @include mobile {
            width: 100%;
        }
    }

    &--visible {
        opacity: 1;
        visibility: visible;
    }

    @include tablet {
        right: -50px;
    }

    @include mobile {
        width: calc(100vw - 30px);
    }
}

.mini-product {
    display: flex;
    align-items: center;
    padding: $offset-xs $offset-m;
    // .mini-product__image

    &__image {
        flex-shrink: 0;
        width: 100px;
        height: 100px;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            background-color: $color-amber-hover;
        }

        @include mobile {
            width: 70px;
            height: 70px;
        }
    }

    // .mini-product__content

    &__content {
        display: flex;
        align-items: center;
        flex-grow: 1;
    }

    // .mini-product__text

    &__text {
        margin-right: auto;
        margin-left: $offset-xs;
        max-width: 80%;

        @include mobile {
            max-width: 70%;
        }
    }

    // .mini-product__title

    &__title {
        margin: 0;
        margin-bottom: $offset-xs;
        font: $font-xs;
        color: $color-black;
    }

    // .mini-product__price

    &__price {
        font: $font-m;
        color: $color-black;
    }

    &__delete {
        font: $font-xs;
        color: $color-white;

        @include small-tablet {
            margin-right: 10px;
            font-size: 12px;
        }
    }

    @include mobile {
        padding: $offset-xs;
    }
}

.burger {
    @include burger;

    display: none;

    @include tablet {
        z-index: 100;
        display: block;
    }
}
