.faq {
    padding: $offset-xl 0;
    background-color: $color-midnight-blue;

    &__title {
        margin-top: 0;
        margin-bottom: $offset-l;
        line-height: 1;
    }
}

.faq-accordion {
    border-top: 1px solid $color-amber;
    border-bottom: 1px solid $color-amber;

    &:not(:first-child) {
        margin-top: -1px;
    }

    // .faq-accordion__icon

    &__icon {
        display: inline-block;
        min-width: 25px;
        min-height: 25px;
        background: url("../../img/cross.svg") no-repeat center top/cover;
        transition: transform 0.3s ease-out;
        padding-right: $offset-xs;
    }

    // .faq-accordion__title

    &__title {
        padding-right: 10px;
    }

    // .faq-accordion__control

    &__control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 30px 0;
        width: 100%;
        font: $font-m;
        text-align: left;
        color: $color-white;
        background-color: transparent;
        cursor: pointer;
        transition: color 0.3s ease-in-out, padding 0.3s ease-in-out;

        &:focus {
            color: $color-amber;
        }

        &:active {
            color: $color-amber;
        }

        @include desktop {
            &:hover {
                padding-left: 30px;
                color: $color-amber;
            }
        }

        @include mobile {
            font-size: 18px;
        }
    }

    // .faq-accordion__content

    &__content {
        overflow: hidden;
        padding-right: 70px;
        padding-bottom: 0;
        max-height: 0;
        box-sizing: content-box;
        font: $font-s;
        color: $color-white;
        opacity: 0;
        transition: all 0.3s ease-out;
        will-change: max-height;
    }
}

.open .faq-accordion__control {
    padding: 20px 0 15px 0;
}

.open .faq-accordion__icon {
    transform: rotate(-45deg);
}

.open .faq-accordion__content {
    padding-bottom: 18px;
    opacity: 1;
}
