.team {
    position: relative;
    overflow: hidden;
    padding: $offset-xl 0;
    background-color: $color-midnight;
    z-index: 1;

    //. team__figure

    &__figure {
        position: absolute;
        right: 0%;
        top: 0;
        z-index: -1;

        @include small-tablet {
            display: none;
        }
    }

    // .team__title

    &__title {
        margin-top: 0;
        margin-bottom: $offset-l;
        color: $color-white;
    }

    // .team__list

    &__list {
        grid-gap: $offset-l 13px;
    }

    // .team__item

    &__item {
        grid-column: 4 span;

        @include small-tablet {
            grid-column: 6 span;
        }
    }

    // .team__photo

    &__photo {
        display: block;
        margin-bottom: $offset-m;
        border-radius: 8px;
        overflow: hidden;

        @include small-tablet {
            width: 100%;
        }
    }

    // .team__subtitle

    &__subtitle {
        display: block;
        margin-bottom: $offset-xs;
        color: $color-white;
        font: $font-l;
        font-weight: 700;

        // @include small-tablet {
        //     font-size: 18px;
        // }
    }

    // .team__post

    &__post {
        display: block;
        color: $color-white;
        font: $font-s;

        // @include small-tablet {
        //     font-size: 14px;
        // }
    }
}
