.catalog {
    padding: $offset-xl 0;
    background-color: $color-midnight-blue;

    // .catalog__left

    &__left {
        grid-column: 3 span;

        @include tablet {
            display: none;
        }
    }

    // .catalog__title

    &__title {
        margin-top: 0;
        color: $color-white;
    }

    // .catalog__subtitle

    &__subtitle {
        margin: 0;
        font: $font-l;
        color: $color-amber;
        margin-bottom: $offset-m;
    }

    // .catalog__property

    &__property {
        margin-bottom: $offset-m;
    }

    // .catalog__caption

    &__caption {
        display: block;
        margin-bottom: $offset-xs;
        font: $font-s;
        color: $color-white;
    }

    // .catalog__price

    &__price {
        display: block;
        margin-bottom: $offset-xs;
    }

    // .catalog__property-checkboxes

    &__property-checkboxes {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        & .custom-checkbox:not(:last-child) {
            margin-bottom: $offset-xs;
            padding-right: $offset-xs;
        }
    }

    // .catalog__grid

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-column: 9 span;

        @include tablet {
            grid-column: 12 span;
        }
    }

    // .catalog__list

    &__list {
        grid-template-columns: repeat(9, 1fr);

        @include small-tablet {
            grid-template-columns: repeat(6, 1fr);
        }
    }

    // .catalog__item

    &__item {
        grid-column: 3 span;

        @include mobile {
            grid-column: 6 span;
        }
    }

    // .catalog__more

    &__more {
        margin-top: $offset-l;
    }
}

.catalog-price {
    // .catalog-price__wrapper

    &__wrapper {
        position: relative;
        display: flex;
        flex-wrap: wrap;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 1px;
            height: 34px;

            background-color: $color-white;
        }
    }

    // .catalog-price__input

    &__input {
        width: 50%;
        height: 50px;
        border: 1px solid $color-white;
        outline: none;
        background-color: transparent;
        text-align: center;
        color: $color-white;
        font: $font-s;

        &:first-child {
            border-right: none;
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
        }

        &:last-child {
            border-left: none;
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
        }
    }

    .noUi-horizontal {
        height: 2px;
    }

    .noUi-target {
        margin-top: -1px;
        border: none;
        box-shadow: none;
        background-color: $color-amber;
    }
    .noUi-connect {
        background-color: $color-white;
    }

    .noUi-horizontal .noUi-handle {
        top: -8px;
        width: 5px;
        height: 18px;
    }

    .noUi-handle {
        top: -8px;
        right: 0;
        border: none;
        border-radius: 100px;
        box-shadow: none;
        background-color: $color-white;
        cursor: pointer;

        &::before,
        &::after {
            display: none;
        }
    }

    .noUi-handle-lower {
        right: -3px;
    }
}

.product {
    // .product__image

    &__image {
        position: relative;

        width: 100%;
        border-radius: 30px;

        max-height: 290px;
        margin-bottom: $offset-xs;

        background-color: rgba($color: $color-midnight, $alpha: 0.3);

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;

            @include desktop {
                opacity: 0.9;
            }
        }

        @include desktop {
            &:hover {
                .product__actions {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        @include mobile {
            max-height: 100%;
        }
    }

    // .product__actions

    &__actions {
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        background-color: rgba($color: $color-midnight, $alpha: 0.8);
        border-radius: 30px;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

        @include tablet {
            opacity: 1;
            visibility: visible;
            background-color: rgba($color: $color-black, $alpha: 0.1);
        }
    }

    // .product__button

    &__button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        width: 80px;
        height: 80px;
        background-color: $color-amber;
        transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out;

        &--disabled {
            opacity: 0.4;
            pointer-events: none;
        }

        svg {
            fill: $color-white;
            width: 30px;
            height: 30px;
            object-fit: cover;
        }

        @include desktop {
            &:hover {
                background-color: $color-amber-hover;
            }
        }

        &:first-child {
            margin-right: 40px;

            @include tablet {
                margin-right: 60px;
            }
        }

        @include tablet {
            position: absolute;
            top: 5%;
            right: 5%;
            width: 55px;
            height: 55px;
        }
    }

    // .product__title

    &__title {
        margin: 0;
        margin-bottom: $offset-xs;
        font: $font-s;
        color: $color-white;
    }

    // .product__price

    &__price {
        font: $font-m;
        color: $color-white;
    }
}

.catalog-filters {
    border-radius: 30px;
    padding: $offset-m;
    background-color: rgba($color: $color-midnight, $alpha: 0.3);

    &__actions {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__apply {
        margin-bottom: $offset-m;
        padding: 22px 47px;
        width: 100%;
        font: $font-s;
        font-weight: 700;
        background-color: $color-amber;
    }

    &__reset {
        font: $font-s-bold;
        color: $color-white;
        transition: color 0.3s ease-in-out;

        @include desktop {
            &:hover {
                color: $color-white;
            }
        }

        &:focus {
            color: $color-white;
        }

        &:active {
            color: red;
        }
    }
}

.table-sizes {
    border-collapse: collapse;
    width: 100%;

    caption {
        position: absolute;
        opacity: 0;
        pointer-events: none;
    }

    td {
        border: 1px solid $color-amber;
        padding: 16px 5px;
        text-align: center;
        color: $color-white;
        font: $font-s;
        transition: background-color 0.3s ease-in-out;
        cursor: pointer;

        @include desktop {
            &:hover {
                background-color: $color-amber;
            }
        }
    }
}

.catalog-price__input.focus-visible {
    color: $color-white;
    background-color: $color-amber;
}
