// Цвета
@import "mixins";

$color-white: #fff;
$color-black: #000;

$color-charcoal: #000814;
$color-midnight-blue: #032b50;
$color-midnight: #001d3d;
$color-jumbo: #8c8c8c;
$color-amber: #ffc300;
$color-amber-hover: #e0ac00;
$color-amber-active: #aa8200;

$color-border: rgba(255, 255, 255, 0.5);

// Отступы
$grid-gap: 20px;

$offset-xl: 60px;
$offset-l: 40px;
$offset-m: 20px;
$offset-xs: 10px;

// Работа с текстом
$font-family: "Intro", sans-serif;

$font-xl: 700 30px / 100% $font-family;
$font-l: 400 24px / 24px $font-family;
$font-m: 600 20px / 20px $font-family;
$font-s-bold: 600 16px / 140% $font-family;
$font-s: 400 16px / 140% $font-family;
$font-xs: 400 14px / 100% $font-family;

// Бургер

$burger-width: 32px;
$burger-height: 20px;
$burger-line-height: 3px;
