.cart-modal {
    width: 580px;
    padding: $offset-l;

    background-color: $color-midnight;
    border-radius: 30px;

    .modal__close {
        display: none;
        z-index: 100;
        top: 15px;
        right: 15px;
        width: 17px;
        height: 17px;

        @include tablet {
            display: block;
        }
    }

    // .cart-modal__top

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: $offset-l;

        @include small-tablet {
            display: block;
            margin-bottom: $offset-m;
        }
    }

    // .cart-modal__title

    &__title {
        margin: 0;
        font: $font-m;
        color: $color-amber;

        @include small-tablet {
            margin-bottom: $offset-xs;
        }
    }

    // .cart-modal__order-number

    &__order-number {
        font: $font-xs;
        color: $color-white;
    }

    // .cart-modal__button

    &__button {
        width: 100%;
    }

    // .cart-modal__input

    &__input {
        width: 100%;
        background-color: $color-midnight-blue;
        color: $color-white;
        margin-bottom: $offset-xs;
    }

    @include small-tablet {
        max-width: 570px;
        width: 100%;
        padding: $offset-m;
    }
}

.cart-modal-order {
    border: 1px solid $color-jumbo;
    border-radius: 30px;
    margin-bottom: $offset-l;
    padding: 30px;
    // .cart-modal-order__top

    &__top {
        font: $font-s;
        color: $color-white;

        span {
            font: $font-s-bold;
            color: $color-amber;
        }
    }

    // .cart-modal-order__quantity

    &__quantity {
        margin-bottom: $offset-m;
    }

    // .cart-modal-order__sum

    &__sum {
        margin-bottom: $offset-m;
    }

    // .cart-modal-order__show

    &__show {
        display: inline-flex;
        align-items: center;
        margin-bottom: $offset-xs;
        font: $font-s;
        color: $color-amber;

        svg {
            margin-left: 5px;
            width: 10px;
            height: 10px;
            fill: currentColor;
            object-fit: cover;
            transform: rotate(180deg);
        }

        &--active {
            svg {
                transform: rotate(0deg);
            }
        }
    }

    // .cart-modal-order__list

    &__list {
        display: none;

        &--visible {
            display: block;
        }
    }

    // .cart-modal-order__item

    &__item {
        &:nth-child &:not(:last-child) {
            margin-bottom: $offset-l;
        }
    }

    // .cart-modal-order__product

    &__product {
        display: flex;
        align-items: center;
    }

    @include small-tablet {
        padding: $offset-xs;
        margin-bottom: $offset-m;
    }
}

.cart-modal {
    .mini-product {
        padding: 0;
        // .mini-product__image

        &__image {
            flex-shrink: 0;
            width: 80px;
            height: 45px;

            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        // .mini-product__content

        &__content {
            display: flex;
            align-items: center;
            flex-grow: 1;
        }

        // .mini-product__text

        &__text {
            margin-right: auto;
            margin-left: $offset-xs;
            max-width: 80%;
        }

        // .mini-product__title

        &__title {
            margin: 0;
            font: $font-xs;
            color: $color-white;
            margin-bottom: $offset-xs;
        }

        // .mini-product__price

        &__price {
            font: $font-s-bold;
            color: $color-white;
        }

        // .mini-product__delete

        &__delete {
            margin: 0;
            font: $font-xs;
            color: $color-white;
            padding-right: 5px;
        }
    }
}
