.footer {
    background-color: $color-charcoal;
    // .footer__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: $offset-m;

        @include tablet {
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer__left

    &__left {
        @include tablet {
            margin-bottom: $offset-m;
        }
    }

    // .footer__wrap

    &__menu {
        .menu__list {
            @include small-tablet {
                flex-wrap: wrap;
                justify-content: center;
            }
        }

        .menu__item {
            @include small-tablet {
                margin-bottom: $offset-xs;
            }
        }
    }
}
