.modal {
    @include tablet {
        &::before {
            display: none;
        }
    }
}

.prod-modal {
    padding: $offset-l;
    border-radius: 30px;
    background-color: $color-midnight;

    .modal__close {
        top: 20px;
        right: 20px;
        display: none;
        z-index: 100;
        fill: $color-amber;

        @include tablet {
            display: block;
        }
    }

    // .prod-modal__top

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: $offset-l;

        @include small-tablet {
            flex-direction: column;
            margin-bottom: 0;
        }
    }

    // .prod-modal__bottom

    &__bottom {
        display: flex;
        @include mobile {
            flex-direction: column;
        }
    }

    // .prod-modal__column

    &__column {
        width: 50%;

        &:first-child {
            margin-right: $offset-l;

            @include small-tablet {
                margin-right: 0;
            }
        }

        @include small-tablet {
            width: 100%;
        }
    }

    // .prod-modal__video

    &__video {
        margin-top: $offset-l;
        width: 100%;
        height: 450px;

        iframe {
            border: none;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    @include tablet {
        margin: 0;
        padding: $offset-m;
        width: 100%;
    }

    @include small-tablet {
        padding: $offset-xs;
    }
}

.modal-slider {
    margin-right: $offset-m;
    width: 50%;
    // .modal-slider__container

    &__container {
        overflow: hidden;
        width: 100%;
        margin-bottom: $offset-m;

        img {
            width: 100%;
            max-height: 460px;
            border-radius: 30px;
            object-fit: cover;
            background-color: rgba($color: $color-white, $alpha: 0.2);
        }
    }

    @include small-tablet {
        margin-bottom: $offset-m;
        margin-right: 0;
        width: 100%;
    }
}

.modal-preview {
    display: flex;
    justify-content: space-between;
    // .modal-preview__item

    &__item {
        width: 90px;
        height: 75px;
        cursor: pointer;
        transition: opacity 0.7s;
        flex-shrink: unset;

        &:not(:last-child) {
            margin-right: 20px;
        }

        @include desktop {
            &:hover {
                opacity: 0.7;
            }
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 20px;
            object-fit: cover;
            background-color: rgba($color: $color-white, $alpha: 0.2);
        }

        &--active {
            opacity: 0.5;
            pointer-events: none;
        }
    }
}

.modal-info {
    width: 50%;

    // .modal-info__top

    &__top {
        margin-bottom: $offset-l;
    }

    // .modal-info__vendor

    &__vendor {
        margin-right: $offset-l;
        font: $font-s;
        color: $color-white;

        @include tablet {
            margin-right: $offset-xs;
        }
    }

    // .modal-info__quantity

    &__quantity {
        font: $font-s;
        color: $color-white;

        span {
            color: $color-white;
        }
    }

    // .modal-info__title

    &__title {
        margin: 0;
        margin-bottom: $offset-m;
        font: $font-l;
        line-height: 33px;
        color: $color-white;
    }

    // .modal-info__rate

    &__rate {
        margin-bottom: $offset-m;
        img:not(:last-child) {
            margin-right: 4px;
        }
    }

    // .modal-info__sizes

    &__sizes {
        margin-bottom: $offset-l;

        @include tablet {
            margin-bottom: $offset-m;
        }
    }

    // .modal-info__subtitle

    &__subtitle {
        display: inline-block;
        margin-bottom: $offset-m;
        font: $font-s;
        color: $color-white;
    }

    // .modal-info__price

    &__price {
        display: flex;
        align-items: center;
        margin-bottom: 75px;

        @include tablet {
            margin-bottom: $offset-m;
        }
    }

    // .modal-info__current-price

    &__current-price {
        font: $font-xl;
        color: $color-white;
        margin-right: 30px;
    }

    // .modal-info__old-price

    &__old-price {
        font: $font-s;
        color: $color-white;
    }

    // .modal-info__order

    &__order {
        max-width: 300px;
        width: 100%;
        margin-bottom: $offset-m;
    }

    //.modal-info__note-list

    &__note-list {
        margin-bottom: $offset-m;
    }

    // .modal-info__note-item

    &__note-item {
        display: block;
        font: $font-xs;
        color: $color-white;

        &::before {
            content: "";
            display: inline-block;
            margin-right: $offset-xs;
            background: url("../../img/check-list.svg") no-repeat left center/ cover;
            width: 15px;
            height: 11px;
        }

        &:not(:last-child) {
            margin-bottom: $offset-xs;
        }
    }

    @include small-tablet {
        margin: 0;
        width: 100%;
    }
}

.modal-sizes {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    // .modal-sizes__item

    &__item {
        margin-bottom: $offset-xs;
        &:not(:last-child) {
            margin-right: $offset-m;
        }
    }

    // .modal-sizes__button

    &__button {
        padding: 10px 0;
        width: 70px;
        border: 1px solid $color-amber;
        border-radius: 30px;
        font: $font-xs;
        transition: background-color 0.3s;
        color: $color-white;

        @include desktop {
            &:hover {
                background-color: $color-amber;
            }
        }

        @include small-tablet {
            width: 60px;
        }
    }
}

.prod-bottom {
    // .prod-bottom__title

    &__title {
        margin-bottom: $offset-m;
        font: $font-l;
        color: $color-white;
    }

    // .prod-bottom__descr

    &__descr {
        max-width: 95%;
        font: $font-s;
        color: $color-white;
    }
}

.prod-bottom-feature {
    // .prod-bottom-feature__item

    &__item {
        display: block;
        color: $color-white;

        &:not(:last-child) {
            margin-bottom: $offset-xs;
        }
    }
}

.modal__container {
    border-radius: 30px;

    @include tablet {
        border-radius: 0;
    }
}
