.contact {
    padding: $offset-xl 0;
    background-color: $color-midnight;
    // .contact__container

    &__container {
        display: flex;
        justify-content: space-between;

        @include tablet {
            flex-wrap: wrap;
        }
    }

    // .contact__text

    &__text {
        margin-right: $offset-m;
        width: calc(100% - (680px + $offset-m));

        @include tablet {
            margin-right: 0;
            margin-bottom: $offset-m;
        }
    }

    // .contact__title

    &__title {
        margin-bottom: $offset-xl;
        margin-top: 0;

        @include mobile {
            margin-bottom: $offset-l;
        }
    }

    // .contact__item

    &__item {
        font-style: normal;
        margin-bottom: $offset-l;
    }

    // .contact__subtitle

    &__subtitle {
        margin-bottom: $offset-m;
        letter-spacing: 1.3px;
        text-transform: uppercase;
        font: $font-xs;
        color: $color-jumbo;

        @include mobile {
            order: 1;
        }
    }

    // .contact__phone

    &__phone {
        display: inline-block;
        margin-bottom: $offset-xs;
        font: $font-xl;
        color: $color-white;

        &:active {
            color: $color-amber;
            transition: color 0.3s ease-in-out;
        }
    }

    // .contact__address

    &__address {
        display: block;
        font: 400 18px / 140% $font-family;
        color: $color-white;

        @include mobile {
            font-size: 14px;
        }
    }

    // .contact__map

    &__map {
        width: 680px;
        height: 500px;

        @include tablet {
            width: 100%;
            height: 100%;
        }
    }

    // .contact__group

    &__group {
        display: flex;
        align-items: center;
    }
}

.social {
    display: flex;
    // .social__item

    &__item {
        &:not(:last-child) {
            margin-right: $offset-m;
        }
    }

    // .social__link

    &__link {
        display: block;
        width: 44px;
        height: 44px;
        transition: opacity 0.8s ease-in-out;
    }

    // .social__link--fb

    &__link--fb {
        background: url("../../img/vk.svg") no-repeat center top/cover;
    }

    // .social__link-insta

    &__link--insta {
        background: url("../../img/insta.svg") no-repeat center top/cover;
    }
}
