.hero {
    position: relative;

    height: 530px;

    background-color: $color-midnight;

    // .hero__container

    &__container {
        position: relative;
        z-index: 5;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        height: 100%;
    }

    // .hero__bg-text

    &__bg-text {
        position: absolute;
        top: 60%;
        left: 50%;
        z-index: 0;
        transform: translate(-50%, -50%);

        font-size: 200px;
        line-height: 200px;
        font-weight: 700;
        font-family: $font-family;

        user-select: none;

        color: rgba(255, 255, 255, 0.12);

        @include tablet {
            display: none;
        }
    }

    // .hero__title

    &__title {
        margin: 0;
        margin-top: 60px;
        max-width: 47%;
        margin-bottom: $offset-m;

        font: 700 36px / 140% $font-family;
        color: $color-white;

        @include tablet {
            max-width: 60%;
        }

        @include small-tablet {
            max-width: none;
        }

        @include mobile {
            font-size: 30px;
        }
    }

    // .hero__descr

    &__descr {
        margin: 0;
        max-width: 40%;
        margin-bottom: $offset-l;

        font: $font-s;
        color: $color-white;

        @include small-tablet {
            max-width: none;
        }
    }

    @include small-tablet {
        height: auto;
        padding: $offset-l 0;
    }
}
