.about {
    position: relative;
    z-index: 1;
    overflow: hidden;
    background-color: $color-midnight;

    //. about__figure

    &__figure {
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
    }

    // .about__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    // .about__text

    &__text {
        max-width: 41%;
        transform: translateY(30px);

        @include small-tablet {
            max-width: 100%;
            transform: none;
        }
    }

    // .about__title

    &__title {
        margin-top: 0;
        margin-bottom: $offset-m;
        color: $color-white;
    }

    // .about__descr

    &__descr {
        margin: 0;
        font-size: $font-s;
        line-height: 23px;
        color: $color-white;
    }

    // .about__quote

    &__quote {
        display: inline-block;
        margin-bottom: $offset-m;
        quotes: none;
    }

    // .about__author

    &__author {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font: $font-m;
        font-style: normal;
        transform: translateX(30px);

        &::before {
            content: "";
            margin-right: $offset-m;
            width: 30px;
            height: 3px;
            background-color: $color-white;
        }

        @include small-tablet {
            transform: none;
        }
    }

    // .about__image

    &__image {
        margin-right: -100px;
        width: 700px;
        img {
            display: block;
            max-width: 100%;
            clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
        }

        @include tablet {
            margin-right: -200px;
        }

        @include small-tablet {
            display: none;
        }
    }

    @include small-tablet {
        padding: $offset-l 0;
    }
}
