.quiz {
    padding: $offset-xl 0;

    background-color: $color-midnight-blue;
    // .quiz__content

    &__content {
        padding: $offset-l 0;

        @include small-tablet {
            padding: $offset-l 0;
        }

        @include mobile {
            padding: 0;
        }
    }

    // .quiz__title

    &__title {
        margin-top: 0;
        margin-bottom: $offset-m;

        @include small-tablet {
            font-size: 28px;
        }
    }

    // .quiz__descr

    &__descr {
        padding-bottom: $offset-xs;
        font: $font-s;
        color: $color-jumbo;
    }

    @include mobile {
        padding: $offset-l 0;
    }
}

.quiz-questions {
    padding: $offset-m 0;
    border-top: 1px solid $color-amber;
    border-bottom: 1px solid $color-amber;
}

.quiz-question {
    &__title {
        margin-top: 0;
        margin-bottom: $offset-m;
        font: $font-l;
        color: $color-white;

        @include small-tablet {
            font-size: 18px;
        }
    }

    &__answers {
        display: grid;
        grid-template-columns: repeat(15, 1fr);
        grid-gap: $grid-gap;

        @include tablet {
            grid-template-columns: repeat(12, 1fr);
        }
    }

    &__message {
        display: block;
        border: none;
        border-radius: 4px;
        padding: $offset-m;
        width: 100%;
        height: 246px;
        background-color: rgba($color: $color-black, $alpha: 0.1);
        color: $color-white;
        resize: none;
        outline: none;

        &::placeholder {
            color: $color-amber;
        }
    }

    &__label {
        display: flex;
    }

    &__count {
        font: $font-s;
        color: $color-jumbo;
    }

    &__item {
        grid-column: 3 span;

        @include small-tablet {
            grid-column: 6 span;
        }
    }

    &__image {
        position: relative;

        width: 100%;
        border-radius: 30px;

        margin-bottom: $offset-xs;

        background-color: rgba($color: $color-white, $alpha: 0.1);

        img {
            display: inline;
            height: 20%;
            object-fit: cover;
            opacity: 0.9;
            margin: 0 auto;
        }
    }
}

.quiz-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: $offset-m;
}

.last-question {
    display: none;
}

.last-question {
    position: relative;
    border-radius: 30px;
    padding: $offset-l;
    max-width: 655px;
    background-color: rgba($color: $color-black, $alpha: 0.1);
    // .last-question__form

    &__form {
        padding-top: $offset-m;
    }

    // .last-question__title

    &__title {
        margin-top: 0;
        margin-bottom: $offset-xs;
        font-size: 36px;
        color: $color-white;

        @include small-tablet {
            font-size: 30px;
        }
    }

    // .last-question__descr

    &__descr {
        font-size: 18px;
        color: $color-white;
        margin: 0;
        margin-bottom: $offset-m;
    }

    // .last-question__input

    &__input {
        margin-bottom: 10px;
        width: 500px;
        max-width: 100%;

        &:nth-of-type(2) {
            margin-bottom: 20px;
        }
    }

    // .last-question__button

    &__button {
        width: 265px;
        max-width: 100%;
    }

    // .last-question__illustration

    &__illustration {
        position: absolute;
        top: -1%;
        right: -185px;

        @include small-tablet {
            display: none;
        }
    }

    @include small-tablet {
        max-width: 100%;
        padding: $offset-m;
    }
}
